<template>
    <div>

        <van-nav-bar title="人才推荐" left-text="返回" left-arrow @click-left="home">
            <van-icon name="search" slot="right"/>
        </van-nav-bar>

        <van-cell title="影响力人才推荐" class="author-name"/>
        <van-cell-group v-for="(y,i) in yxl" @click="method_1(i)">
            <van-card :desc="y.desc" :title="y.name" thumb="docs/glass_72.png" class="narrow">
                <div slot="tags">
                    <van-tag plain type="danger" class="ttaagg" v-show="y.gyx">影响力人才</van-tag>
                    <van-tag plain type="primary" class="ttaagg" v-show="y.gcc">高产出人才</van-tag>
                    <van-tag plain type="success" class="ttaagg" v-show="y.gby">高被引人才</van-tag>
                </div>
            </van-card>
        </van-cell-group>

        <p></p>
        <van-cell title="创新潜力人才推荐" class="author-name"/>
        <van-cell-group v-for="(y,i) in ql" @click="method_2(i)">
            <van-card :desc="y.desc" :title="y.name" thumb="docs/phd_72.png" class="narrow">
                <div slot="tags">
                    <van-tag plain type="danger" class="ttaagg" v-show="y.gyx">影响力人才</van-tag>
                    <van-tag plain type="primary" class="ttaagg" v-show="y.gcc">高产出人才</van-tag>
                    <van-tag plain type="success" class="ttaagg" v-show="y.gby">高被引人才</van-tag>
                </div>
            </van-card>
        </van-cell-group>


    </div>
</template>

<script>
    import {Checkbox, CheckboxGroup, Card, SubmitBar, Toast, NavBar, Tag, Pagination, Search} from 'vant';
    import {ContactCard, ContactList, ContactEdit} from 'vant';
    import {Cell, CellGroup} from 'vant';
    import axios from 'axios';


    export default {
        components: {
            [Card.name]: Card,
            [Checkbox.name]: Checkbox,
            [SubmitBar.name]: SubmitBar,
            [CheckboxGroup.name]: CheckboxGroup,
            [NavBar.name]: NavBar,
            [Tag.name]: Tag,
            [Pagination.name]: Pagination,
            [Search.name]: Search,
            [ContactCard.name]: ContactCard,
            [ContactList.name]: ContactList,
            [ContactEdit.name]: ContactEdit,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
        },

        data() {
            return {
                currentPage: 1,
                checkedGoods: ['1', '2', '3']
            };
        },


        methods: {
            method_1(i) {
                // console.log(ev) // this is the event
                // console.log(i) // i is index of v-for
                this.$router.push('person/' + this.yxl[i].tag);
            },
            method_2(i) {
                // console.log(ev) // this is the event
                // console.log(i) // i is index of v-for
                this.$router.push('person/' + this.ql[i].tag);
            },

            onSubmit() {
                Toast('点击结算');
            },

            home() {
                this.$router.push('user');
            },
            onInit() {


                const path = `api/tuijian`;
                axios.get(path)
                    .then(response => {
                        console.log(response.data)
                        this.ql = response.data.ql
                        this.yxl = response.data.yxl


                        for (var i = 0; i < this.yxl.length; i++) {
                            this.yxl[i]['desc'] = "专利个数:" + this.yxl[i]['a0'] + ','
                                + "专利H指数:" + this.yxl[i]['a6'] + ',' +
                                "总被引次数:" + this.yxl[i]['a1']

                            this.yxl[i]['name'] = this.yxl[i]['tag'].split('|')[0];

                            if (this.yxl[i].a0 >= 7) {
                                this.yxl[i]['gcc'] = true
                            } else {
                                this.yxl[i]['gcc'] = false
                            }

                            if (this.yxl[i].a1 >= 2) {
                                this.yxl[i]['gby'] = true
                            } else {
                                this.yxl[i]['gby'] = false
                            }

                            if (this.yxl[i].a6 >= 2) {
                                this.yxl[i]['gyx'] = true
                            } else {
                                this.yxl[i]['gyx'] = false
                            }
                        }

                        for (var i = 0; i < this.ql.length; i++) {
                            this.ql[i]['desc'] = "专利个数:" + this.ql[i]['a0'] + ','
                                + "专利H指数:" + this.ql[i]['a6'] + ',' +
                                "总被引次数:" + this.ql[i]['a1']

                            this.ql[i]['name'] = this.ql[i]['tag'].split('|')[0];

                            if (this.ql[i].a0 >= 7) {
                                this.ql[i]['gcc'] = true
                            } else {
                                this.ql[i]['gcc'] = false
                            }

                            if (this.ql[i].a1 >= 2) {
                                this.ql[i]['gby'] = true
                            } else {
                                this.ql[i]['gby'] = false
                            }

                            if (this.ql[i].a6 >= 2) {
                                this.ql[i]['gyx'] = true
                            } else {
                                this.ql[i]['gyx'] = false
                            }
                        }

                        this.$forceUpdate()
                    })
                    .catch(error => {
                        console.log(error)
                    })

            },
        },
        created() {
            this.onInit();
        },
    };
</script>
<style lang="less">
    .author-name {
        font-size: 18px;
    }

    .ttaagg {
        margin-left: 5px;
    }

    .narrow {
        height: 85px;
    }
</style>